import { ENGINE } from '../engine';
import { ResizeNearestNeighbor, ResizeNearestNeighborGrad } from '../kernel_names';
export const resizeNearestNeighborGradConfig = {
  kernelName: ResizeNearestNeighbor,
  inputsToSave: ['images'],
  gradFunc: (dy, saved, attrs) => {
    const [images] = saved;

    const backPropKernelFunc = backend => {
      const {
        alignCorners
      } = attrs;
      return backend.resizeNearestNeighborBackprop(dy, images, alignCorners);
    };

    const inputs = {
      images
    };

    const imagesDer = () => ENGINE.runKernelFunc(backPropKernelFunc, inputs, null
    /* gradient */
    , ResizeNearestNeighborGrad, attrs);

    return {
      images: imagesDer
    };
  }
};