import { Unpack } from '../kernel_names';
import { stack } from '../ops/stack';
export const unpackGradConfig = {
  kernelName: Unpack,
  gradFunc: (dy, saved, attrs) => {
    const unpackAttrs = attrs;
    const {
      axis
    } = unpackAttrs;
    return {
      value: () => stack(dy, axis)
    };
  }
};