import { ENGINE } from '../engine';
import { Cumsum } from '../kernel_names';
import { convertToTensor } from '../tensor_util_env';
import { getAxesPermutation, getInnerMostAxes, getUndoAxesPermutation } from './axis_util';
import { op } from './operation';
import { transpose } from './transpose';
/**
 * Computes the cumulative sum of a `tf.Tensor` along `axis`.
 *
 * ```js
 * const x = tf.tensor([1, 2, 3, 4]);
 * x.cumsum().print();
 * ```
 * ```js
 * const x = tf.tensor([[1, 2], [3, 4]]);
 * x.cumsum().print();
 * ```
 *
 * @param x The input tensor to be summed.
 * @param axis The axis along which to sum. Optional. Defaults to 0.
 * @param exclusive Whether to perform exclusive cumulative sum. Optional.
 *     Defaults to false. If set to true then the sum of each tensor entry
 *     does not include its own value, but only the values previous to it
 *     along the specified axis.
 * @param reverse Whether to sum in the opposite direction. Optional.
 *     Defaults to false.
 *
 * @doc {heading: 'Operations', subheading: 'Scan'}
 */

function cumsum_(x, axis = 0, exclusive = false, reverse = false) {
  const $x = convertToTensor(x, 'x', 'cumsum');

  const forward = (backend, save) => {
    const permutation = getAxesPermutation([axis], $x.rank);
    let permutedX = $x;

    if (permutation != null) {
      permutedX = transpose($x, permutation);
    }

    const permutedAxis = getInnerMostAxes(1, $x.rank)[0];
    let value = backend.cumsum(permutedX, permutedAxis, exclusive, reverse);
    save([$x]);

    if (permutation != null) {
      const reversePermutation = getUndoAxesPermutation(permutation);
      value = transpose(value, reversePermutation);
    }

    return value;
  };

  const inputs = {
    x: $x
  };
  const attrs = {
    axis,
    exclusive,
    reverse
  };
  return ENGINE.runKernelFunc(forward, inputs, null
  /* grad */
  , Cumsum, attrs);
}

export const cumsum = op({
  cumsum_
});