import { GreaterEqual } from '../kernel_names';
import { zerosLike } from '../ops/zeros_like';
export const greaterEqualGradConfig = {
  kernelName: GreaterEqual,
  inputsToSave: ['a', 'b'],
  gradFunc: (dy, saved) => {
    const [a, b] = saved;
    return {
      a: () => zerosLike(a),
      b: () => zerosLike(b)
    };
  }
};