import { Reverse } from '../kernel_names';
import { reverse } from '../ops/reverse';
import { parseAxisParam } from '../util';
export const reverseGradConfig = {
  kernelName: Reverse,
  gradFunc: (dy, saved, attrs) => {
    const {
      dims
    } = attrs;
    const axes = parseAxisParam(dims, dy.shape);
    return {
      x: () => reverse(dy, axes)
    };
  }
};